.problems {
  padding-bottom: $pb-section-desctop;
  &__inner {}
  &__title {
    max-width: 760px;
    &.ready {
      color: #008857;
    }
  }
  &__info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 60px;
    &.hidden {
      display: none;
    }
  }
  &__info-row {
    font-size: 18px;
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
      color: #6C6A72;
    }
    &:first-child {
      font-weight: 500;
    }
    &.not-zero {
      font-weight: 500;
      & > .problems__info-count {
        color: #3E4DA6;
      }
    }
  }
  &__info-count {
    color: #6C6A72;
  }
  &__btn {
    margin-bottom: 60px;
    &.hidden {
      display: none;
    }
  }
  &__list {
    display: flex;
  }
}

.problems-card {
  background: #FFFFFF;
  // box-shadow: 3px 4px 30px rgba(62, 77, 166, 0.1);
  border-radius: 10px;
  padding: 20px 12px 0 15px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  cursor: pointer;
  user-select: none;
  transition: box-shadow .15s;
  &.is-shadow {
    box-shadow: 3px 4px 30px rgba(62, 77, 166, 0.1);
  }
  &.is-visible {
    box-shadow: 3px 4px 30px rgba(62, 77, 166, 0.1);
  }
  &__cb {
    display: block;
    width: 30px;
    height: 30px;
    border: 1px solid #000000;
    border-radius: 50%;
    margin: 0 auto;
    cursor: pointer;
    margin-bottom: 20px;
    position: relative;
    &::after {
      content: '';
      display: block;
      position: absolute;
      width: 36px;
      height: 36px;
      left: -3px;
      top: -7px;
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
    }
    &.active {
      border-color: $color-accent2;
      &::after {
        background-image: url(../img/icon/check.svg);
      }
    }
    &.done {
      border-color: #008857;
      &::after {
        background-image: url(../img/icon/check-green.svg);
      }
    }
    & > input {opacity: 0;}
  }
  &__title {
    font-weight: 500;
    font-size: 18px;
    line-height: 27px;
    color: $color-subtitle;
    margin-bottom: 10px;
  }
  &__text {
    font-size: 16px;
    line-height: 24px;
    color: $color-main-text2;
    flex-grow: 1;
  }
  &__img {
    margin-bottom: 0;
    margin-right: 0;
    margin-left: auto;
  }
}