.reviews-card { //TODO так же поработать с отступами и тенью
  background-color: #ffffff;
  border-radius: 10px;
  padding: 30px;
  transition: box-shadow .15s;
  box-shadow: none;
  &.is-shadow {
    box-shadow: 3px 4px 30px rgba(62, 77, 166, 0.1);
  }
  &.is-visible {
    box-shadow: 3px 4px 30px rgba(62, 77, 166, 0.1);
  }
  &__header {
    padding-bottom: 20px;
    margin-bottom: 20px;
    border-bottom: 1px solid #D8E0FF;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__name {
    font-weight: 500;
    font-size: 18px;
    color: $color-subtitle;
  }
  &__info {
    color: #6C6A72;
  }
  &__text {
    line-height: 1.5;
  }
}

