html {

}
body {
  font-family: 'Roboto', sans-serif;
  font-size: $fz-plain-desctop;
  font-weight: 400;

  color: $color-main-text;
}
.container {
  max-width: 1220px;
  padding-left: 10px;
  padding-right: 10px;
  margin: 0 auto;
  width: 100%;
}
.red-text {
  color: $color-accent2;
}
.btn {
  padding: 20px 40px;
  text-transform: uppercase;
  color: #FFFFFF;
  background-color: $color-accent2;
  border-radius: 10px;
  position: relative;
  z-index: 1;
  border: none;
  cursor: pointer;
  display: block;
  transition: .15s;
  &::after {
    content: '';
    display: block;
    position: absolute;
    z-index: -1;
    left: 40px;
    right: 40px;
    height: 20px;
    bottom: 0;
    box-shadow: 0 0 40px 5px $color-accent2;
    transition: .25s;
    border-radius: 50%;
  }
  &:hover::after {
    box-shadow: 0 0 15px 0px $color-accent2;
  }
  &:active {
    transform: translateY(2px);
    background-color: #d34039;
  }
  &:active::after {
    box-shadow: 0 0 15px 0px transparent;
  }
}

.rating {
  display: flex;
  &__star {
    width: 20px;
    height: 20px;
    background: no-repeat center / contain;
    background-image: url(../img/icon/star.svg);
    margin-right: 5px;
  }
}

.section-title {
  font-size: $fz-section-title-desctop;
  line-height: $lh-section-title-desctop;
  color: $color-accent1;
  margin-bottom: $mb-section-title-desctop;
  font-weight: 700;
}

.splide {
  &:not(.non-shadow) {
    width: calc(100% + 40px);
    margin-left: -20px;
  }
  &__track {
    padding-top: 20px;
    padding-bottom: 20px;
  }

}
//FIXME поработать с тенью и отступами
.splide__pagination {
  display: flex;
  justify-content: center;
  width: 100%;
  & > li {
    margin-right: 17px;
  }
  & > li:last-child {
    margin-right: 0;
  }
}

.splide__pagination__page  {  //FIXME баг, прыгают точки при перелистывании
  display: block;
  padding: 0;
  background-color: transparent;
  width: 12px;
  height: 12px;
  border: 1px solid #D3D7F2;
  border-radius: 50%;
  cursor: pointer;
  position: relative;
  &.is-active {
  border: 1px solid #3E4DA6;
    &::after {
      content: '';
      width: 8px;
      height: 8px;
      border-radius: 50%;
      position: absolute;
      left: 1px;
      top: 1px;
      background-color: #3E4DA6;
    }
  }
}

.splide__arrow {
  position: absolute;
  display: block;
  z-index: 2;
  top: 50%;
  transform: translateY(-50%);
  background-color: transparent;
  border: none;
  cursor: pointer;
  &--prev {
    left: -70px;
  }
  &--next {
    right: -70px;
  }
  &[disabled] {
    opacity: 0.35;
  }
}



@media (max-width: 1400px) {
  .splide__arrow {
    &--prev {
      left: -40px;
    }
    &--next {
      right: -40px;
    }
  }
  .splide {
    &.non-shadow {
      .splide__arrow {
        &--prev {
          left: -70px;
        }
        &--next {
          right: -70px;
        }
      }
    }
  }
}