.quiz {
  padding-bottom: $pb-section-desctop;
  &__inner {}
  &__title {}
  &__content {}
  &__count {
    font-size: 50px;
    font-weight: 900;
    color: #D8E0FF;
    margin-bottom: 30px;
  }
  &__question {
    font-weight: 500;
    font-size: 18px;
    line-height: 1.5;
    color: $color-subtitle;
    margin-bottom: 30px;
  }
  &__row {
    display: flex;
    justify-content: space-between;
  }
  &__answer {
    position: relative;
    padding: 18px 75px 18px 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid transparent;
    color: $color-subtitle;
    border-radius: 10px;
    cursor: pointer;
    line-height: 1;
    box-shadow: 3px 4px 30px 0px rgba(62, 77, 166, 0.1);
    user-select: none;
    transition: .15s;
    &.checked {
      border: 1px solid $color-accent2;
      color: $color-accent2;
    }
    &.checked .quiz__radio-label {
      border: 1px solid $color-accent2;
      &::after {
        background-color: $color-accent2;
      }
    }
  }
  &__radio-label {
    position: relative;
    z-index: 1;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    border: 1px solid #3E4DA6;
    cursor: pointer;
    margin-right: 10px;
    &::after {
      content: '';
      width: 8px;
      height: 8px;
      display: block;
      background-color: transparent;
      position: absolute;
      z-index: 1;
      left: 1px;
      top: 1px;
      border-radius: 50%;
    }
    & input[type="radio"] {
      padding: 0;
      margin: 0;
      position: absolute;
      z-index: -1;
      visibility: hidden;
    }
  }
  &__btn {
    display: flex;
    align-items: center;
    position: relative;
    padding-right: 70px;
    &::before {
      content: '';
      display: block;
      position: absolute;
      right: 40px;
      top: calc(50% - 5px);
      background: no-repeat center / contain;
      background-image: url(../img/icon/arrow-small.svg);
      width: 18px;
      height: 10px;
    }
  }
}