@charset "UTF-8";
*, *::before, *::after {
  box-sizing: border-box;
}

ul, ol {
  padding: 0;
}

body, h1, h2, h3, h4, p, ul, ol, li, figure, figcaption, blockquote, dl, dd {
  margin: 0;
}

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: normal;
}

ul, ol {
  list-style: none;
}

a {
  text-decoration: inherit;
  color: inherit;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

img {
  max-width: 100%;
  display: block;
}

input, button, textarea, select {
  font: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

body {
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: #605975;
}

.container {
  max-width: 1220px;
  padding-left: 10px;
  padding-right: 10px;
  margin: 0 auto;
  width: 100%;
}

.red-text {
  color: #EC4A42;
}

.btn {
  padding: 20px 40px;
  text-transform: uppercase;
  color: #FFFFFF;
  background-color: #EC4A42;
  border-radius: 10px;
  position: relative;
  z-index: 1;
  border: none;
  cursor: pointer;
  display: block;
  transition: .15s;
}

.btn::after {
  content: '';
  display: block;
  position: absolute;
  z-index: -1;
  left: 40px;
  right: 40px;
  height: 20px;
  bottom: 0;
  box-shadow: 0 0 40px 5px #EC4A42;
  transition: .25s;
  border-radius: 50%;
}

.btn:hover::after {
  box-shadow: 0 0 15px 0px #EC4A42;
}

.btn:active {
  transform: translateY(2px);
  background-color: #d34039;
}

.btn:active::after {
  box-shadow: 0 0 15px 0px transparent;
}

.rating {
  display: flex;
}

.rating__star {
  width: 20px;
  height: 20px;
  background: no-repeat center / contain;
  background-image: url(../img/icon/star.svg);
  margin-right: 5px;
}

.section-title {
  font-size: 40px;
  line-height: 60px;
  color: #3E4DA6;
  margin-bottom: 60px;
  font-weight: 700;
}

.splide:not(.non-shadow) {
  width: calc(100% + 40px);
  margin-left: -20px;
}

.splide__track {
  padding-top: 20px;
  padding-bottom: 20px;
}

.splide__pagination {
  display: flex;
  justify-content: center;
  width: 100%;
}

.splide__pagination > li {
  margin-right: 17px;
}

.splide__pagination > li:last-child {
  margin-right: 0;
}

.splide__pagination__page {
  display: block;
  padding: 0;
  background-color: transparent;
  width: 12px;
  height: 12px;
  border: 1px solid #D3D7F2;
  border-radius: 50%;
  cursor: pointer;
  position: relative;
}

.splide__pagination__page.is-active {
  border: 1px solid #3E4DA6;
}

.splide__pagination__page.is-active::after {
  content: '';
  width: 8px;
  height: 8px;
  border-radius: 50%;
  position: absolute;
  left: 1px;
  top: 1px;
  background-color: #3E4DA6;
}

.splide__arrow {
  position: absolute;
  display: block;
  z-index: 2;
  top: 50%;
  transform: translateY(-50%);
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.splide__arrow--prev {
  left: -70px;
}

.splide__arrow--next {
  right: -70px;
}

.splide__arrow[disabled] {
  opacity: 0.35;
}

@media (max-width: 1400px) {
  .splide__arrow--prev {
    left: -40px;
  }
  .splide__arrow--next {
    right: -40px;
  }
  .splide.non-shadow .splide__arrow--prev {
    left: -70px;
  }
  .splide.non-shadow .splide__arrow--next {
    right: -70px;
  }
}

.header__top {
  background-color: #0A274F;
  padding: 11px 0;
}

.header__contacts {
