  align-items: center;
}

.header__tel, .header__geo {
  display: flex;
  align-items: center;
  color: #FFFFFF;
  font-weight: 500;
}

.header__tel::before, .header__geo::before {
  content: '';
  display: block;
  order: -2;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  width: 25px;
  height: 25px;
}

.header__tel::after, .header__geo::after {
  content: '';
  display: block;
  order: -1;
  width: 1px;
  height: 2em;
  background-color: #203C69;
  margin-right: 12px;
  margin-left: 12px;
}

.header__tel {
  margin-right: 52px;
}

.header__tel::before {
  background-image: url(../img/icon/phone.svg);
}

.header__geo::before {
  background-image: url(../img/icon/geo.svg);
}

.header__middle {
  padding: 15px 0;
}

.header__logo {
  margin: 0 auto;
  max-width: 345px;
}

.header__bottom {
