}

.problems {
  padding-bottom: 120px;
}

.problems__title {
  max-width: 760px;
}

.problems__title.ready {
  color: #008857;
}

.problems__info {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 60px;
}

.problems__info.hidden {
  display: none;
}

.problems__info-row {
  font-size: 18px;
  margin-bottom: 20px;
}

.problems__info-row:last-child {
  margin-bottom: 0;
  color: #6C6A72;
}

.problems__info-row:first-child {
  font-weight: 500;
}

.problems__info-row.not-zero {
  font-weight: 500;
}

.problems__info-row.not-zero > .problems__info-count {
  color: #3E4DA6;
}

.problems__info-count {
  color: #6C6A72;
}

.problems__btn {
  margin-bottom: 60px;
}

.problems__btn.hidden {
  display: none;
}

.problems__list {
  display: flex;
}

.problems-card {
  background: #FFFFFF;
  border-radius: 10px;
  padding: 20px 12px 0 15px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  cursor: pointer;
  user-select: none;
  transition: box-shadow .15s;
}

.problems-card.is-shadow {
  box-shadow: 3px 4px 30px rgba(62, 77, 166, 0.1);
}

.problems-card.is-visible {
  box-shadow: 3px 4px 30px rgba(62, 77, 166, 0.1);
}

.problems-card__cb {
  display: block;
  width: 30px;
  height: 30px;
  border: 1px solid #000000;
  border-radius: 50%;
  margin: 0 auto;
  cursor: pointer;
  margin-bottom: 20px;
  position: relative;
}

.problems-card__cb::after {
  content: '';
  display: block;
  position: absolute;
  width: 36px;
  height: 36px;
  left: -3px;
  top: -7px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.problems-card__cb.active {
  border-color: #EC4A42;
}

.problems-card__cb.active::after {
  background-image: url(../img/icon/check.svg);
}

.problems-card__cb.done {
  border-color: #008857;
}

.problems-card__cb.done::after {
  background-image: url(../img/icon/check-green.svg);
}

.problems-card__cb > input {
  opacity: 0;
}

.problems-card__title {
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  color: #0A274F;
  margin-bottom: 10px;
}

.problems-card__text {
  font-size: 16px;
  line-height: 24px;
  color: #6C6A72;
  flex-grow: 1;
}

.problems-card__img {
  margin-bottom: 0;
  margin-right: 0;
  margin-left: auto;
}

.reason {
  padding-bottom: 120px;
}

.reason__title {
  margin-bottom: 30px;
}

.reason__content {
  display: flex;
  justify-content: space-between;
  position: relative;
