}

.reason__text::after {
  content: '';
  display: block;
  position: absolute;
  z-index: -1;
  background-color: #F6F8FF;
  width: 50%;
  left: -100px;
  top: 0;
  height: 100%;
}

.reason__img {
  position: absolute;
