}

.chart {
  padding-bottom: 120px;
  overflow: hidden;
}

.chart__content {
  display: flex;
  justify-content: space-between;
  background: #F7F8FF;
  position: relative;
}

.chart__content::after {
  content: '';
  display: block;
  position: absolute;
  z-index: -1;
  top: 0;
  right: -200px;
  height: 100%;
  width: 50%;
  background: #F7F8FF;
}

.chart__board {
  flex-basis: 50%;
  max-width: 50%;
  background-color: #ffffff;
}

.chart__text {
  flex-basis: 50%;
  max-width: 50%;
  padding: 30px;
}

.chart__text > p {
  margin-bottom: 30px;
}

.chart__text b {
  font-weight: 500;
  font-size: 18px;
}

.chart-board {
  box-shadow: 3px 4px 30px rgba(62, 77, 166, 0.1);
  border-radius: 10px;
  padding: 25px 15px 50px 50px;
  padding-bottom: 100px;
}

.chart-board__title {
  padding-bottom: 25px;
  font-weight: 500;
  color: #0A274F;
  font-size: 18px;
}

.chart-board__box {
  border-bottom: 1px solid #A7A5AD;
  border-left: 1px solid #A7A5AD;
  height: 100%;
  position: relative;
}

.chart-board__box::before, .chart-board__box::after {
  position: absolute;
  display: block;
  color: #A7A5AD;
}

.chart-board__box::before {
  content: attr(data-y);
  left: -20px;
  top: 0;
  transform-origin: left;
  transform: rotate(-90deg) translateX(-100%);
}

.chart-board__box::after {
  content: attr(data-x);
  bottom: -30px;
  right: 0;
}

.chart-board__line {
  background-image: url(../img/content/chart-line.svg);
  background-repeat: no-repeat;
  background-position: 0 bottom;
  background-size: 100% auto;
  height: 100%;
  width: 100%;
  position: absolute;
}

.chart-board__icon {
  background-image: url(../img/icon/angry.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  height: 32px;
  width: 32px;
  position: absolute;
  right: 6%;
  top: 16%;
}

.chart-board__icon::before {
  content: attr(data-text);
  position: absolute;
  left: 50%;
  top: -1.5em;
  color: #C10000;
  display: inline-block;
  transform: translateX(-50%);
}

.tools {
  padding-bottom: 120px;
}

.tools__row {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
}

.tools__img {
  width: 200px;
  min-width: 200px;
}

.tools__img > img {
  margin: 0 auto 0 0;
}

.tools__text p {
  margin-bottom: 15px;
  color: #6C6A72;
}

.tools__text b {
  font-weight: 500;
  color: #0A274F;
  font-size: 18px;
}

.results {
  padding-bottom: 120px;
}

.results-card {
  display: flex;
  flex-direction: column;
}

.results-card.is-shadow .results-card__box {
  box-shadow: 3px 4px 30px rgba(62, 77, 166, 0.1);
}

.results-card.is-visible .results-card__box {
  box-shadow: 3px 4px 30px rgba(62, 77, 166, 0.1);
}

.results-card::before {
  font-weight: 900;
  font-size: 40px;
  color: #C7CFEF;
  text-align: left;
}

.results-card--before::before {
  content: 'Было';
}

.results-card--after::before {
  content: 'Стало';
}

.results-card__box {
  background-color: #ffffff;
  border-radius: 10px;
  padding: 30px;
  flex-grow: 1;
  transition: box-shadow .15s;
}

.results-card__header {
  padding-bottom: 20px;
  margin-bottom: 20px;
  border-bottom: 1px solid #D8E0FF;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.results-card__name {
  font-weight: 500;
  font-size: 18px;
  color: #0A274F;
}

.results-card__info {
  color: #6C6A72;
}

.results-card__text {
  line-height: 1.5;
}

.themes {
  padding-bottom: 120px;
}

.themes__subtitle {
  font-weight: 500;
  font-size: 18px;
  color: #0A274F;
  margin-bottom: 60px;
}

.themes__slider {
  margin-bottom: 60px;
}

.themes__btn {
  margin: 0 auto;
}

.themes-card {
  padding: 30px 40px 30px 25px;
  transition: box-shadow .15s;
}

.themes-card.is-shadow {
  box-shadow: 3px 4px 30px rgba(62, 77, 166, 0.1);
}

.themes-card.is-visible {
  box-shadow: 3px 4px 30px rgba(62, 77, 166, 0.1);
}

.themes-card__title {
  font-weight: 500;
  font-size: 18px;
  color: #3E4DA6;
  margin-bottom: 20px;
}

.themes-card__text {
  line-height: 24px;
  color: #6C6A72;
}

.differences {
  padding-bottom: 120px;
}

.differences__title {
  margin-bottom: 80px;
}

.differences__table {
  background: #F7F8FF;
  border-radius: 10px;
}

.differences__row {
  display: flex;
  justify-content: space-between;
  position: relative;
  /* нижняя граница строки */
  /* ========== */
  /* верхняя и нижняя плашка в средней колонке */
  /* ======= */
  /* нижняя граница каждого элемента во второй колонке */
  /* ========== */
}

.differences__row::after {
  content: '';
  background: #F1F1F2;
  display: block;
  height: 1px;
  width: calc(100% - 60px);
  position: absolute;
  z-index: 1;
  left: 30px;
  bottom: 0;
}

.differences__row:last-of-type::after {
  display: none;
}

.differences__row:first-of-type .differences__cell:nth-of-type(2)::before,
.differences__row:last-of-type .differences__cell:nth-of-type(2)::before {
  content: '';
  position: absolute;
  display: block;
  background-color: #EC4A42;
  width: 100%;
  height: 1.5em;
  left: 0;
  right: 0;
}

.differences__row:first-of-type .differences__cell:nth-of-type(2)::before {
  top: -1.5em;
  border-top-left-radius: 21px;
  border-top-right-radius: 21px;
}

.differences__row:last-of-type .differences__cell:nth-of-type(2)::before {
  bottom: -1.5em;
  border-bottom-left-radius: 21px;
  border-bottom-right-radius: 21px;
}

.differences__row .differences__cell:nth-of-type(2)::after {
  content: '';
  display: block;
  height: 1px;
  width: 84%;
  position: absolute;
  bottom: 0;
  left: 8%;
  background: #E7ECFF;
}

.differences__row:last-of-type .differences__cell:nth-of-type(2)::after {
  display: none;
}

.differences__cell {
  padding: 25px 50px;
  position: relative;
  z-index: 2;
}

.differences__cell:nth-of-type(1) {
  flex: 0 0 38%;
  color: #6C6A72;
}

.differences__cell:nth-of-type(2) {
  background-color: #EC4A42;
  flex: 0 0 31%;
  color: #FFFFFF;
}

.differences__cell:nth-of-type(3) {
  flex: 0 0 31%;
  color: #6C6A72;
}

.quiz {
  padding-bottom: 120px;
}

.quiz__count {
  font-size: 50px;
  font-weight: 900;
  color: #D8E0FF;
  margin-bottom: 30px;
}

.quiz__question {
  font-weight: 500;
  font-size: 18px;
  line-height: 1.5;
  color: #0A274F;
  margin-bottom: 30px;
}

.quiz__row {
  display: flex;
  justify-content: space-between;
}

.quiz__answer {
  position: relative;
  padding: 18px 75px 18px 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid transparent;
  color: #0A274F;
  border-radius: 10px;
  cursor: pointer;
  line-height: 1;
  box-shadow: 3px 4px 30px 0px rgba(62, 77, 166, 0.1);
  user-select: none;
  transition: .15s;
}

.quiz__answer.checked {
  border: 1px solid #EC4A42;
  color: #EC4A42;
}

.quiz__answer.checked .quiz__radio-label {
  border: 1px solid #EC4A42;
}

.quiz__answer.checked .quiz__radio-label::after {
  background-color: #EC4A42;
}

.quiz__radio-label {
  position: relative;
  z-index: 1;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: 1px solid #3E4DA6;
  cursor: pointer;
  margin-right: 10px;
}

.quiz__radio-label::after {
  content: '';
  width: 8px;
  height: 8px;
  display: block;
  background-color: transparent;
  position: absolute;
  z-index: 1;
  left: 1px;
  top: 1px;
  border-radius: 50%;
}

.quiz__radio-label input[type="radio"] {
  padding: 0;
  margin: 0;
  position: absolute;
  z-index: -1;
  visibility: hidden;
}

.quiz__btn {
  display: flex;
  align-items: center;
  position: relative;
  padding-right: 70px;
}

.quiz__btn::before {
  content: '';
  display: block;
  position: absolute;
  right: 40px;
  top: calc(50% - 5px);
  background: no-repeat center / contain;
  background-image: url(../img/icon/arrow-small.svg);
  width: 18px;
  height: 10px;
}

.leading {
