}

.leading__title {
  margin-bottom: 30px;
}

.leading__content {
  position: relative;
  padding-right: 130px;
}

.leading__text {
  position: relative;
  background-color: #F6F8FF;
  line-height: 24px;
  border-top-right-radius: 150px;
  padding: 30px 400px 30px 0;
}

.leading__text::after {
  content: '';
  display: block;
  position: absolute;
  z-index: -1;
  background-color: #F6F8FF;
  width: 50%;
  left: -100px;
  top: 0;
  height: 100%;
}

.leading__intro {
  margin-bottom: 60px;
  max-width: 500px;
  color: #0A274F;
}

.leading__row {
  margin-bottom: 55px;
  font-size: 18px;
  font-weight: 500;
  color: #6C6A72;
}

.leading__accent {
  color: #3E4DA6;
  font-size: 35px;
