}

.leading__img {
  position: absolute;
  right: 0;
  bottom: 0;
  max-width: 350px;
}

.reviews {
  padding-bottom: 120px;
}

.reviews-card {
  background-color: #ffffff;
  border-radius: 10px;
  padding: 30px;
  transition: box-shadow .15s;
  box-shadow: none;
}

.reviews-card.is-shadow {
  box-shadow: 3px 4px 30px rgba(62, 77, 166, 0.1);
}

.reviews-card.is-visible {
  box-shadow: 3px 4px 30px rgba(62, 77, 166, 0.1);
}

.reviews-card__header {
  padding-bottom: 20px;
  margin-bottom: 20px;
  border-bottom: 1px solid #D8E0FF;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.reviews-card__name {
  font-weight: 500;
  font-size: 18px;
  color: #0A274F;
}

.reviews-card__info {
  color: #6C6A72;
}

.reviews-card__text {
  line-height: 1.5;
}

.request {
  padding-bottom: 120px;
}

.request__title {
  margin-bottom: 30px;
}

.request__subtitle {
  font-weight: 500;
  font-size: 18px;
  color: #0A274F;
  margin-bottom: 45px;
}

.request__row {
  display: flex;
  align-items: flex-end;
  margin-bottom: 25px;
}

.request__box {
  position: relative;
  padding-top: 16px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  max-width: 295px;
  width: 100%;
  margin-right: 35px;
}

.request__box::before {
  content: '';
  display: block;
  width: 111px;
  height: 94px;
  position: absolute;
  z-index: -1;
  top: -16px;
  left: -20px;
  background: no-repeat center / contain;
}

.request__box:nth-of-type(1)::before {
  background-image: url(../img/bg/rhombus-blue.svg);
}

.request__box:nth-of-type(2)::before {
  background-image: url(../img/bg/rhombus-gray.svg);
}

.request__label {
  margin-bottom: 3px;
}

.request__input {
  max-width: none;
  width: 100%;
  padding: 20px 25px 15px;
  box-shadow: 3px 4px 30px rgba(62, 77, 166, 0.1);
  border: none;
  border-radius: 10px;
}

.request__input::placeholder {
  color: rgba(163, 172, 186, 0.7);
}

.request__policy {
  font-size: 11px;
}

.request__policy-label {
  position: relative;
  cursor: pointer;
}

.request__policy-label::before, .request__policy-label::after {
  cursor: pointer;
}

.request__policy-label::before {
  content: '';
  display: block;
  width: 10px;
  height: 10px;
  border: 0.5px solid #3E4DA6;
  border-radius: 1px;
  position: absolute;
  top: 50%;
  margin-top: -5px;
}

.request__policy-label::after {
  content: '';
  display: none;
  width: 10px;
  height: 10px;
  position: absolute;
  top: 50%;
  margin-top: -5px;
  background: no-repeat center / contain;
  background-image: url(../img/icon/check.svg);
}

.request__policy-label.checked::after {
  display: block;
}

.request__cb {
  margin: 0;
  padding: 0;
  opacity: 0;
  visibility: hidden;
}

.news {
  padding-bottom: 120px;
}

.news__title {
  margin-bottom: 50px;
}

.news-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  position: relative;
}

.news-grid__item {
  border-radius: 10px;
  max-width: 33%;
  padding-bottom: 30%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  margin-bottom: 20px;
  position: relative;
  overflow: hidden;
}

.news-grid__item:hover .news-grid__img {
  transform: scale(1.05);
}

.news-grid__item .news-grid__title {
  font-size: 22px;
  line-height: 33px;
  font-weight: 700;
}

.news-grid__item:hover .news-grid__title {
  text-decoration-color: #0A274F;
}

@supports (flex: 0 0 calc(33.333% - 13px)) {
  .news-grid__item {
    flex: 0 0 calc(33.333% - 13px);
  }
}

.news-grid__item--large {
  max-width: 66%;
}

@supports (flex: 0 0 calc(67% - 10px)) {
  .news-grid__item--large {
    flex: 0 0 calc(67% - 10px);
  }
}

.news-grid__item--left-bottom .news-grid__content {
  top: auto;
  bottom: 25px;
}

.news-grid__item--condensed .news-grid__title {
  margin-bottom: 15px;
}

.news-grid__item--condensed .news-grid__content {
  top: 30px;
  max-width: 220px;
}

.news-grid__content {
  position: absolute;
  top: 25px;
  left: 25px;
  z-index: 2;
}

.news-grid__title {
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  color: #0A274F;
  margin-bottom: 5px;
  text-decoration: underline;
  text-decoration-color: transparent;
  transition: text-decoration-color .25s;
}

.news-grid__subtitle {
  font-size: 18px;
  line-height: 27px;
  color: #6C6A72;
}

.news-grid__img {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  transition: .25s;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.footer {
  background-color: #0A274F;
  padding: 60px 70px;
  color: #FFFFFF;
}

.footer__inner {
  display: flex;
  justify-content: space-between;
}

.footer__column {
  display: flex;
  flex-direction: column;
}

.footer__column:first-of-type {
  max-width: 33%;
  flex-basis: 33%;
  border-right: 1px solid #203C69;
  padding-right: 13%;
}

.footer__column:last-of-type {
  max-width: 50%;
  flex-basis: 50%;
}

.footer__column-title {
  font-weight: 700;
  font-size: 30px;
  margin-bottom: 30px;
}

.footer__column-subtitle {
  margin-bottom: 35px;
  font-size: 18px;
}

.footer__tel, .footer__geo, .footer__mail {
  font-weight: 500;
  margin-bottom: 35px;
  display: flex;
  align-items: center;
}

.footer__tel::before, .footer__geo::before, .footer__mail::before {
  content: '';
  display: block;
  order: -2;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  width: 25px;
  height: 25px;
  min-height: 25px;
  min-width: 25px;
}

.footer__tel::after, .footer__geo::after, .footer__mail::after {
  content: '';
  display: block;
  order: -1;
  width: 1px;
  height: 2em;
  background-color: #203C69;
  margin-right: 12px;
  margin-left: 12px;
}

.footer__tel::before {
  background-image: url(../img/icon/phone.svg);
}

.footer__geo::before {
  background-image: url(../img/icon/geo.svg);
}

.footer__mail::before {
  background-image: url(../img/icon/mail.svg);
}

.footer__social {
  display: flex;
}

.footer__social span {
  margin-right: 10px;
}

.footer__social a {
  display: block;
  transition: .25s;
}

.footer__social a:hover {
  transform: scale(1.25);
}

.footer__form {
  flex-grow: 1;
}

.footer__nav-item {
  display: inline-block;
  margin-right: 20px;
  font-weight: 500;
}

.footer__nav-item:last-child {
  margin-right: 0;
}

.footer__nav-item:hover {
  text-decoration: underline;
}

.footer-form {
  display: flex;
}

.footer-form__box {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  max-width: 295px;
  width: 100%;
}

.footer-form__box:first-of-type {
  margin-right: 25px;
}

.footer-form__box:last-of-type {
  padding-top: calc(1em + 5px);
}

.footer-form__label {
  color: #C8C8C8;
  margin-bottom: 5px;
}

.footer-form__input {
  max-width: none;
  width: 100%;
  padding: 18px 25px;
  box-shadow: 3px 4px 30px rgba(62, 77, 166, 0.1);
  border: none;
  border-radius: 10px;
}

.footer-form__input::placeholder {
  color: rgba(163, 172, 186, 0.7);
}

.footer-form__btn::after {
  display: none;
}
