.footer {
  background-color: #0A274F;
  padding: 60px 70px;
  color: #FFFFFF;

  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__column {
    display: flex;
    flex-direction: column;
    &:first-of-type {
      max-width: 33%;
      flex-basis: 33%;
      border-right: 1px solid #203C69;
      padding-right: 13%;
    }
    &:last-of-type {
      max-width: 50%;
      flex-basis: 50%;
    }
  }
  &__column-title {
    font-weight: 700;
    font-size: 30px;
    margin-bottom: 30px;
  }
  &__column-subtitle {
    margin-bottom: 35px;
    font-size: 18px;
  }
  &__row {}
  &__tel, &__geo, &__mail {
    font-weight: 500;
    margin-bottom: 35px;
    display: flex;
    align-items: center;
    &::before {
      content: '';
      display: block;
      order: -2;
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      width: 25px;
      height: 25px;
      min-height: 25px;
      min-width: 25px;
    }
    &::after {
      content: '';
      display: block;
      order: -1;
      width: 1px;
      height: 2em;
      background-color: #203C69;
      margin-right: 12px;
      margin-left: 12px;
    }
  }
  &__tel::before {
    background-image: url(../img/icon/phone.svg);
  }
  &__geo::before {
    background-image: url(../img/icon/geo.svg);
  }
  &__mail::before {
    background-image: url(../img/icon/mail.svg);
  }
  &__social {
    display: flex;
    & span {
      margin-right: 10px;
    }
    & a {
      display: block;
      transition: .25s;
      &:hover {
        transform: scale(1.25);
      }
    }
  }
  &__form {
    flex-grow: 1;
  }
  &__nav {}
  &__nav-list {
    // display: flex;
  }
  &__nav-item {
    display: inline-block;
    margin-right: 20px;
    font-weight: 500;
    &:last-child {
      margin-right: 0;
    }
    &:hover {
      text-decoration: underline;
    }
  }
  &__nav-link {}
}

.footer-form {
  display: flex;
  // align-items: flex-end;
  &__box {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    max-width: 295px;
    width: 100%;
    &:first-of-type {
      margin-right: 25px;
    }
    &:last-of-type {
      padding-top: calc(1em + 5px);
    }
  }
  &__label {
    color: #C8C8C8;
    margin-bottom: 5px;
  }
  &__input {
    max-width: none;
    width: 100%;
    padding: 18px 25px;
    box-shadow: 3px 4px 30px rgba(62, 77, 166, 0.1);
    border: none;
    border-radius: 10px;
    &::placeholder {
      color: rgba(163, 172, 186, 0.7);
    }
  }
  &__btn {
    &::after {
      display: none;
    }
  }
}