.news {
  padding-bottom: $pb-section-desctop;  //FIXME изменить отступ
  &__inner {}
  &__title {
    margin-bottom: 50px;
  }
  &__slider {}
  &__list {}
}

.news-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  // margin-bottom: 20px;
  position: relative;
  &__item {
    border-radius: 10px;
    max-width: 33%;
    padding-bottom: 30%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    margin-bottom: 20px;
    position: relative;
    overflow: hidden;
    &:hover .news-grid__img {
      transform: scale(1.05);
    }
    & .news-grid__title {
      font-size: 22px;
      line-height: 33px;
      font-weight: 700;
    }
    &:hover .news-grid__title {
      text-decoration-color: $color-subtitle;
    }

    @supports (flex: 0 0 calc(33.333% - 13px)) {
      flex: 0 0 calc(33.333% - 13px);
    }
    &--large {
      max-width: 66%;
      @supports (flex: 0 0 calc(67% - 10px)) {
        flex: 0 0 calc(67% - 10px);
      }
    }
    &--left {}
    &--left-top {}
    &--left-bottom {
      & .news-grid__content {
        top: auto;
        bottom: 25px;
      }
    }
    &--condensed {
      & .news-grid__title {margin-bottom: 15px;}
      & .news-grid__content {
        top: 30px;
        max-width: 220px;
      }
    }
  }
  &__content {
    position: absolute;
    top: 25px;
    left: 25px;
    z-index: 2;
  }
  &__title {
    font-weight: 500;
    font-size: 18px;
    line-height: 27px;
    color: $color-subtitle;
    margin-bottom: 5px;
    text-decoration: underline;
    text-decoration-color: transparent;
    transition: text-decoration-color .25s;
  }
  &__subtitle {
    font-size: 18px;
    line-height: 27px;
    color: #6C6A72;
  }
  &__img {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    transition: .25s;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
}